import React, { useState, useMemo, useCallback } from 'react';
import { useStore } from '../../store';
import './styles/Position.scss';

// Modules

// Types
import { ProtocolInfo } from '../../constants/protocolConfig';

// Comps
import SearchBar from './SearchBar';

// Material
import { Grid, useMediaQuery } from '@mui/material';
import { PositionInfoMap } from '../../constants/position';
import { StrategyInfo } from '../../constants/strategy';
import { BN, getVaultName } from '../../helpers/utilities';
import textContext from '../../textContext';
import TableLoader from '../Investment/TableLoader';
import { Box } from '../v1/Components/Box';
import { Button } from '../v1/Components/Button';
import TablePositions from './TablePositions';
import { PositionInfo } from '../../types';

interface IPositionProps {
  data: StrategyInfo[];
  extraPositions: PositionInfo[] | null;
  positions: PositionInfoMap;
  strategyType: 'PDN' | 'LO';
}

const Table = (props: any) => {
  const language = useStore((state) => state.appSlice.language);
  const text = textContext[language as keyof object]['portfolio']['order'];
  const [sort, setSort] = useState<
    '' | 'position' | 'tvl' | 'capacity' | 'deposit' | 'value' | 'yields'
  >('');
  const ArowDown = (props: any) => (
    <span className={`${props.active ? 'active' : ''} sorting-icon`}>
      {' '}
      &#x25bc;{' '}
    </span>
  );

  const UpdateSort = (
    sortType:
      | ''
      | 'position'
      | 'tvl'
      | 'capacity'
      | 'deposit'
      | 'value'
      | 'yields'
  ) => {
    sort === sortType ? setSort('') : setSort(sortType);
    props.onSelect!(sortType);
  };

  const GenerateGrid = (start: number, end: number) => {
    const setting = [
      { size: 4, name: text[0].text, sort: 'position' },
      // { size: 1.5, name: text[3].text, sort: 'deposit' },
      { size: 2, name: text[4].text, sort: 'value' },
      // { size: 1.45, name: text[5].text, sort: 'yields' },
    ];

    return setting.slice(start, end).map((item, i) => {
      return (
        <Grid item md={item.size} key={i}>
          <div className="subtitle-2" onClick={() => UpdateSort(item.sort)}>
            {item.name} <ArowDown active={sort === item.sort} />
          </div>
        </Grid>
      );
    });
  };

  return (
    <Grid
      className="tableItems"
      justifyContent="space-between"
      container
      style={{ padding: '0px 16px', alignItems: 'center' }}
    >
      {GenerateGrid(0, 1)}
      <Grid item md={3}>
        <span className="subtitle-2" onClick={() => UpdateSort('tvl')}>
          {' '}
          {text[1].text} <ArowDown active={sort === 'tvl'} />
        </span>
        {' / '}
        <span className="subtitle-2" onClick={() => UpdateSort('capacity')}>
          {text[2].text} <ArowDown active={sort === 'capacity'} />
        </span>
      </Grid>
      {GenerateGrid(1, 4)}
      <Grid item md={3}>
        <div className="subtitle-2">Action</div>
      </Grid>
    </Grid>
  );
};

export default function PositionList({ ...props }: IPositionProps) {
  const { data, positions, extraPositions } = props;
  const isPC = useMediaQuery('(min-width:1200px)');

  // const [positions, setPositions] = useState<IPositionDetails[] | null>(
  //   positionDetails
  // );

  const [sort, setSort] = useState<string | null>(null);
  const [reverse, setReverse] = useState<boolean>(false);
  const [open, setOpen] = useState<boolean>(false);
  const [isOpen, setIsOpen] = useState<boolean>(false);
  // const [def, setDefault] = useState<IPositionDetails | null>(null);
  const totalLength = useMemo(() => {
    return (
      Object.values(positions).length +
      (extraPositions ? extraPositions.length : 0)
    );
  }, [extraPositions, positions]);
  const [slice, handleSlice] = useState<number>(
    totalLength > 10 ? 10 : totalLength
  );
  const [search, setSearch] = React.useState('');

  const handleSearch = (fil: string) => {
    setSearch(fil);
  };

  const handleSort = useCallback(
    (a: StrategyInfo, b: StrategyInfo) => {
      let getA = !reverse ? a : b;
      let getB = !reverse ? b : a;

      const poolSort = () =>
        getB.tokens[0].name < getA.tokens[0].name ? -1 : 1;
      const tvlSort = () => BN(getB.TVL).minus(getA.TVL).toNumber();
      const capacitySort = () =>
        BN(getB.capacity).minus(getA.capacity).toNumber();
      const valueSort = () =>
        positions
          ? BN(positions[getB.strategyId]?.equity ?? 0)
              .minus(positions[getA.strategyId]?.equity ?? 0)
              .toNumber()
          : 0;

      switch (sort) {
        case 'position':
          return poolSort();
        case 'tvl':
          return tvlSort();
        case 'capacity':
          return capacitySort();
        case 'value':
          return valueSort();
        default:
          return poolSort();
      }
    },
    [positions, reverse, sort]
  );

  const loaders = new Array(6).fill('');

  const displayExtraPositions = useCallback(() => {
    return extraPositions!.map((item, i) => {
      const strategyInfo = data.filter(
        (x) => x.strategyId === item.strategyId
      )[0];
      const title = getVaultName(strategyInfo.tokens);
      return (
        <TablePositions
          isExtra={true}
          isOpen={setIsOpen}
          title={title}
          allStrategies={data}
          strategyInfo={strategyInfo}
          positionInfo={item}
          key={`x-${i}`}
        />
      );
    });
  }, [data, extraPositions]);

  const displayPositions = useCallback(() => {
    if (Object.values(positions).length === 0) return [];
    return data
      .sort(handleSort)
      .filter(
        (fil) =>
          (getVaultName(fil.tokens)
            .toLowerCase()
            .includes(search.toLowerCase()) ||
            getProtocol(fil.protocols)
              .toLowerCase()
              .includes(search.toLowerCase())) &&
          positions[fil.strategyId] &&
          BN(positions[fil.strategyId].equity).gt(0)
      )
      .map((item, i) => {
        const title = getVaultName(item.tokens);
        return (
          <TablePositions
            isOpen={setIsOpen}
            title={title}
            allStrategies={data}
            strategyInfo={item}
            positionInfo={positions[item.strategyId]}
            key={i}
          />
        );
      });
  }, [data, handleSort, positions, search]);

  const viewMore = () => {
    if (slice + 10 <= totalLength) handleSlice(slice + 10);
    else handleSlice(totalLength);
  };

  const getProtocol = (protocols: ProtocolInfo[]) => {
    const dexes = protocols
      .map((protocol) => protocol.name)
      .join('-')
      .toUpperCase();
    return dexes;
  };

  return (
    <React.Fragment>
      <Box
        style={{
          padding: '32px 24px',
          borderRadius: '0px 0px 8px 8px',
          position: 'relative',
        }}
        className="position-container"
      >
        <SearchBar pageType="portfolio" handleSearch={handleSearch} />
        <div className="investTable">
          <div className="tableHeaders">
            {isPC && (
              <Table
                onSelect={(data: any) => {
                  setSort(data);
                  if (data === sort) setReverse(!reverse);
                }}
              />
            )}

            {totalLength > 0
              ? displayPositions()
                  // Temporary fix for users to be able to see and withdraw their incorrectly opened extra positions on portfolio page.
                  .concat(!extraPositions ? [] : displayExtraPositions())
                  .slice(0, slice)
              : loaders.map((d, i) => <TableLoader key={i} />)}
          </div>
        </div>

        {totalLength && slice !== totalLength && (
          <div className="view-more">
            <Button
              variant="outline"
              size="small"
              text={'view more'}
              onClick={viewMore}
              style={{ padding: '0px 23px' }}
            />
          </div>
        )}
      </Box>
    </React.Fragment>
  );
}
