import { DropdownInfoBox } from '../../../';
import { ReturnMini, Box, Modal } from '../../../../../..';
import { find_x_days_ago, getReturnData } from '../../../../../../../../helpers/graph';
import { PdnStrategyDetails } from '../../../PdnStrategyDetails';
import { AWSDocumentClientContext } from '../../../../../../../../store/provider/AWSProvider'
import { useContext } from 'react';
import { PositionInfo } from '../../../../../../../../types';
import { StrategyInfo } from '../../../../../../../../constants/strategy';

export interface ReturnProps {
  modalChildren: any;
  positionInfo?: PositionInfo;
  strategyInfo: StrategyInfo;
  vaultTitle: any;
}
export const Return = ({
  modalChildren,
  strategyInfo,
  vaultTitle,
  returnData,
  positionInfo
}: ReturnProps) => {
  const { state } = useContext(AWSDocumentClientContext)

  returnData = getReturnData(
    state.graphData.assetPrice,
    state.graphData.homoraPerformance,
    strategyInfo.tokens[0].ticker,
    strategyInfo.tokens[1].ticker
  );

  const week_data = find_x_days_ago(returnData, 7);
  const month_data = find_x_days_ago(returnData, 30);

  const returns = {
    day:find_x_days_ago(returnData, 1),
    week: find_x_days_ago(returnData, 7),
    month: find_x_days_ago(returnData, 30),
    season: find_x_days_ago(returnData, 120),
    inception: returnData,
  };

  return (
    <>
      {
        // TODO: replace with real data.
      }
      <DropdownInfoBox
        title={'Return'}
        viewDetails={{
          buttonText: 'View Details',
          modalChildren,
        }}
        first
      >
        <Modal
          modalClassName="modal-pdn-strategy-details"
          trigger={
            <Box
              className="line-graph-box"
              style={{ margin: '12px 16px 16px', padding: '0px' }}
            >
              <ReturnMini
                details={false}
                color={{ percentage: '#7BEFFF', price: '#19EE94' }}
                data={returns}
                percentage={
                  returnData && returnData.length > 0
                    ? {
                      week: week_data[week_data.length - 1].percentage,
                      month: month_data[month_data.length - 1].percentage,
                      inception: returnData[returnData.length - 1].percentage,
                    }
                    : {
                      week: 0,
                      month: 0,
                      inception: 0,
                    }
                }
                token={
                  returnData && returnData.length > 0
                    ? returnData[returnData.length - 1].ticker
                    : 'Token'
                }
                payload={[
                  {
                    value: 'Return',
                    type: 'line',
                    id: 'percentage',
                    color: '#7BEFFF',
                  },
                  {
                    value: `${returnData && returnData.length > 0
                      ? returnData[returnData.length - 1].ticker
                      : 'Token'
                      } Price`,
                    type: 'line',
                    id: 'price',
                    color: '#19EE94',
                  },
                ]}
                fill={true}
              />
            </Box>
          }
        >
          <PdnStrategyDetails
            strategyInfo={strategyInfo}
            positionInfo={positionInfo}
            vaultTitle={vaultTitle}
            scrollToReturn
          />
        </Modal>
      </DropdownInfoBox>
    </>
  );
};
