import { HtmlComponent } from 'bizcharts/lib/plots/core/dependents';
import {
    forwardRef,
    ButtonHTMLAttributes,
    useRef,
    useImperativeHandle,
    useState,
    useEffect,
} from 'react';
import './index.scss';

interface ButtongGroupsContentProps {
    text: string;
    value: string;
}

interface ButtongGroupsProps {
    input: ButtongGroupsContentProps[];
    onSelect: (value: string) => void;
}

export const ButtonGroups = forwardRef<HtmlComponent, ButtongGroupsProps>(
    ({ input, onSelect }, ref) => {
        const [selected, setSelected] = useState(input[0].value)
        return (
            <div className="selectedButtonList button-3">
                {input.map((item) =>
                    <div
                        className={
                            'selectebutton ' +
                            (selected === item.value ? 'selectedbutton' : 'unselectedbutton')
                        }
                        onClick={(e) => { e.stopPropagation(); setSelected(item.value); onSelect(item.value) }}
                    >
                        {item.text}
                    </div>
                )}
            </div>)
    })

