import { ProtocolInfo } from '../../../../../../constants/protocolConfig';
import { DetailsDropdown } from '../../../DetailsDropdown';
import { BacktestAnalysis, Info, Return } from '../DropdownInfo/PDN';
import { PdnStrategyDetails } from '../PdnStrategyDetails';
import { StrategyInfo } from '../../../../../../constants/strategy/index';
import { Grid } from '@mui/material';
import {
  find_x_days_ago,
  getReturnData,
} from '../../../../../../helpers/graph';
import { PositionInfo } from '../../../../../../types';
import { useContext } from 'react';
import { AWSDocumentClientContext } from '../../../../../../store/provider/AWSProvider'


interface PdnDropdownProps {
  protocols?: ProtocolInfo[];
  vaultTitle?: string;
  accordionClose: any;
  strategyInfo: StrategyInfo;
  positionInfo: PositionInfo;
}

export const PdnDropdown = ({
  accordionClose,
  protocols = [],
  vaultTitle = '',
  strategyInfo,
  positionInfo,
}: PdnDropdownProps) => {
  const mockProps = {
    threshold:
      strategyInfo.priceMovementThreshold / strategyInfo.leverage / 100,
    // sharpeRation: strategyInfo.apy,
    leverage: strategyInfo.leverage,
    modalChildren: (
      <PdnStrategyDetails
        positionInfo={positionInfo}
        strategyInfo={strategyInfo}
        vaultTitle={vaultTitle}
      />
    ),
  };
  const { state } = useContext(AWSDocumentClientContext)
  const backtestAnalysisData = state.graphData.backtestAnalysis
  const returnData = getReturnData(
    state.graphData.assetPrice,
    state.graphData.homoraPerformance,
    strategyInfo.tokens[0].ticker,
    strategyInfo.tokens[1].ticker
  );
  const returns = {
    day: find_x_days_ago(returnData, 1),
    week: find_x_days_ago(returnData, 7),
    month: find_x_days_ago(returnData, 30),
    season: find_x_days_ago(returnData, 120),
    inception: returnData,
  };
  return (
    <DetailsDropdown>
      <Grid container>
        <Grid item xl={2.9} lg={2.9} md={2.9}>
          <Info {...{ ...mockProps, protocols, vaultTitle }} />
        </Grid>
        <Grid item xl={4.55} lg={4.55} md={4.55} sx={{ paddingLeft: '2px' }}>
          <Return
            {...{
              ...mockProps,
              strategyInfo,
              positionInfo,
              vaultTitle,
              returns,
            }}
          />
        </Grid>
        <Grid item xl={4.55} lg={4.55} md={4.55} sx={{ paddingLeft: '2px' }}>
          <BacktestAnalysis
            {...{
              ...mockProps,
              positionInfo,
              strategyInfo,
              vaultTitle,
              backtestAnalysisData,
            }}
          />
        </Grid>
      </Grid>
    </DetailsDropdown>
  );
};
