import { numberToPercentage } from '../../../../helpers/utilities';
import './index.scss';
export interface TooltipProps {
  active: any;
  payload: any;
  label: any;
  type: 'full' | 'mini';
  token?: any;
  color?: any;
}

const Dot = ({ color }) => (
  <div style={{ background: `${color}` }} className="tooltips-dot" />
);

export const Tooltip = ({
  active,
  payload,
  label,
  type = 'full',
  token = '',
  color = { percentage: '#7BEFFF', price: '#19EE94' },
}: TooltipProps) => {
  if (active && payload && payload.length) {
    const dateValue = new Date(label * 1000).toLocaleString('en-us').split(',');

    return (
      <div className="graph-tooltip">
        {type === 'full' && (
          <div className="row-play subtitle2">
            <div>{dateValue[0]}</div>
            <div>{dateValue[1]}</div>
          </div>
        )}
        <div className="row-play subtitle2">
          {type === 'full' && (
            <div>
              <Dot color={color.percentage} />
              Return:{' '}
            </div>
          )}
          <div style={{ color: payload[0].value > 0 ? '#11ffa9' : '#ff9a9a' }}>
            {`${payload[0].value > 0 ? '+' : ''}${numberToPercentage(
              payload[0].value
            )}`}
          </div>
          {type === 'mini' && (
            <div>{new Date(label * 1000).toLocaleDateString('en-US')}</div>
          )}
        </div>
        {payload.length > 1 && (
          <div className="row-play subtitle2">
            {type === 'full' && (
              <div>
                <Dot color={color.price} />
                {token} Price:
              </div>
            )}
            <div style={{ color: 'white' }}>${payload[1].value}</div>
            {type === 'mini' && (
              <div>{new Date(label * 1000).toLocaleDateString('en-US')}</div>
            )}
          </div>
        )}
      </div>
    );
  }

  return null;
};
