import { forwardRef, useRef, useState, useEffect } from 'react';
import { LineGraph } from '../../../../Charts/LineGraph';
import './index.scss';
import { numberToPercentage } from '../../../../../../helpers/utilities';
import { ButtonGroupsSmall } from '../../../../';

interface ReturnMiniProps {
  color: any;
  data: any;
  payload: any;
  token: any;
  details: boolean;
  lineCount?: 1 | 2;
  fill?: boolean;
}

interface dataInfo {
  name: string;
  value: number;
}

interface IPercent {
  week: number;
  month: number;
  inception: number;
}

export const ReturnMini = forwardRef<HTMLDivElement, ReturnMiniProps>(
  (
    {
      details = false,
      color,
      data = { day: [], week: [], month: [], season: [], inception: [] },
      payload,
      token,
      lineCount = 1,
      fill = false,
      ...props
    },
    ref
  ) => {
    const [timeRage, setTimeRage] = useState<String>('day');
    const parentRef = useRef(null);

    const getTime = () => {
      switch (timeRage) {
        case "day":
          return "1-day"
        case "week":
          return "1-week"
        case "month":
          return "1-month"
        case "season":
          return "3-month"
        case "inception":
          return "All"
      }
    }

    return (
      <div ref={parentRef} {...props} className="return-chart">
        {details && (
          <>
            <div className="headline-5 medium" style={{ color: '#FFFFFF' }}>
              Return Chart
            </div>
            <div className="headline-5 grid3-mini">
              Return: <br /><br />
              <span className={`span-1 ${data[timeRage][data[timeRage].length - 1].percentage > 0 ? ' positive' : ' negative'}`}>
                {`${(data[timeRage][data[timeRage].length - 1].percentage * 100).toFixed(2)}% `}
              </span>
              Past {getTime()}
            </div>
          </>
        )}
        <div className="line-graph-container">
          <ButtonGroupsSmall
            input={[{ text: '1D', value: 'day' }, { text: '1W', value: 'week' }, { text: '1M', value: 'month' }, { text: '3M', value: 'season' }, { text: 'ALL', value: 'inception' },]}
            onSelect={(value: string) => setTimeRage(value)}
          />
          <LineGraph
            color={color}
            data={data[timeRage]}
            type="mini"
            legend={details}
            payload={payload}
            token={token}
            fill={fill}
            lineCount={lineCount}
          />
        </div>
      </div>
    );
  }
);
