import React, {
  useState,
  useContext,
  useEffect,
  useMemo,
  useCallback,
} from 'react';
import { useStore } from '../../../../store';
import textContext from '../../../../textContext';
import { Button } from '../../Components/Button';
import './whitelist.scss';
import { startup, whitelist } from '../../../../assets/images/modal';
import CloseIcon from '@mui/icons-material/Close';
import Modal from '@mui/material/Modal';
import { MobilePopOver } from '../../Modal/MobilePopOver';
import { useMediaQuery } from '@mui/material';
import { AWSDocumentClientContext } from '../../../../store/provider/AWSProvider';

const PrivateBetaModal = () => {
  const language = useStore((state) => state.appSlice.language);
  const privateBeta = useStore((state) => state.appSlice.privateBeta);
  const walletChecked = useStore((state) => state.appSlice.walletChecked);
  const setWalletChecked = useStore((state) => state.appSlice.setWalletChecked);
  const walletAddress = useStore(
    (state) => state.chainSlice.chainProvider?.walletAddress
  );
  const deleteWallet = useStore(
    (state) => state.chainSlice.chainProvider?.deleteWallet
  );
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );
  const text = textContext[language].whitelistedConnect;
  const { congratulation, needAccess } = text;
  const { state } = useContext(AWSDocumentClientContext);
  const isPC = useMediaQuery('(min-width:1200px)');
  const [whiteList, setWhiteList] = useState([]);

  useEffect(() => {
    if (state.graphData.whiteList) setWhiteList(state.graphData.whiteList);
  }, [state.graphData.whiteList]);

  function inWhitelist(address: string) {
    if (address) {
      const result = whiteList.filter(
        (fil) => fil.wallet_address.toLowerCase() === address.toLowerCase()
      );
      return result.length > 0 ? true : false;
    }
    return false;
  }

  const handleEnterBeta = useCallback(() => {
    if (walletAddress) {
      setWalletChecked(walletAddress);
    }
    document.body.style.overflow = 'auto';
  }, [walletAddress, setWalletChecked]);

  const handleJoinWhitelist = () => {
    window.open('https://aperturefinance.crew3.xyz/questboard', '_blank');
    handleClose();
  };

  const handleClose = () => {
    document.body.style.overflow = 'auto';

    // TODO: disconnect wallet automatically
    walletAddress && isWalletConnected && deleteWallet && deleteWallet();
  };

  const AccessImg = () => (
    <img
      src={startup}
      alt="Congratulation"
      style={{ marginTop: isPC ? '49px' : '0px' }}
    ></img>
  );

  const NeedAccessImg = () => (
    <img
      src={whitelist}
      alt="Need Access"
      style={{ marginTop: isPC ? '62px' : '22px' }}
    ></img>
  );

  const Content = (gainedAccess: boolean) => (
    <div className={isPC ? 'notification-modal' : 'pop-over-modal'}>
      <div className="content-container">
        {isPC && !gainedAccess && (
          <CloseIcon className="cancel" onClick={handleClose} />
        )}
        <div>{gainedAccess ? <AccessImg /> : <NeedAccessImg />}</div>
        <div className="content">
          <div className={`headline-1${gainedAccess ? '' : ' title'}`}>
            {gainedAccess ? congratulation.header : needAccess.header}
          </div>
          <div className={`description${gainedAccess ? '' : ' access'}`}>
            {gainedAccess ? congratulation.description : needAccess.description}
          </div>
        </div>
        <div className="confirm">
          <Button
            variant="contained"
            size="large"
            light={true}
            text={gainedAccess ? congratulation.confirm : needAccess.confirm}
            onClick={gainedAccess ? handleEnterBeta : handleJoinWhitelist}
          />
        </div>
      </div>
    </div>
  );

  return (
    <React.Fragment>
      {isPC && (
        <React.Fragment>
          <Modal
            open={
              privateBeta &&
              isWalletConnected &&
              inWhitelist(walletAddress!) &&
              !walletChecked.includes(walletAddress!)
            }
            disableEnforceFocus
          >
            {Content(true)}
          </Modal>
          <Modal
            open={
              privateBeta && isWalletConnected && !inWhitelist(walletAddress!)
            }
            disableEnforceFocus
          >
            {Content(false)}
          </Modal>
        </React.Fragment>
      )}
      {!isPC && (
        <React.Fragment>
          <MobilePopOver
            cross={false}
            open={
              privateBeta &&
              isWalletConnected &&
              inWhitelist(walletAddress!) &&
              !walletChecked.includes(walletAddress!)
            }
            className="notification-pop-over"
          >
            {Content(true)}
          </MobilePopOver>
          <MobilePopOver
            cross={true}
            open={
              privateBeta && isWalletConnected && !inWhitelist(walletAddress!)
            }
            onClose={handleClose}
            className="notification-pop-over"
          >
            {Content(false)}
          </MobilePopOver>
        </React.Fragment>
      )}
    </React.Fragment>
  );
};

export default PrivateBetaModal;
