import React from 'react';
import { Box, NumberCounter } from '../../';
import './TvlBox.scss';
import { useMediaQuery } from '@mui/material';
import {
  BN,
  numberParse,
  numberParseLarge,
} from '../../../../helpers/utilities';
export interface TvlBoxProps {
  isPrimary?: boolean;
  heading: string;
  amount: number | string;
  prefix?: string;
  decimals?: number;
  boxWrapper?: boolean;
  style?: any;
}

const Wrapper = ({
  children,
  style,
  boxWrapper,
  ...props
}: {
  children: JSX.Element;
  boxWrapper: boolean;
  style: any;
}) =>
  boxWrapper ? (
    <Box className="tvl-box" style={style}>
      {children}
    </Box>
  ) : (
    <div>{children}</div>
  );
export const TvlBox = ({
  isPrimary = false,
  prefix = '$',
  heading,
  amount,
  decimals = 0,
  boxWrapper = true,
  style,
  ...props
}: TvlBoxProps) => {
  const isPC = useMediaQuery('(min-width:1200px)');
  return (
    <Wrapper {...{ style, boxWrapper, ...props }}>
      <div className="portClass">
        <div
          className={`title ${
            isPrimary ? 'headline-5' : isPC ? 'headline-5' : 'subtitle-3'
          }`}
        >
          {heading}
        </div>
        <span
          className={
            isPrimary ? 'headline-1' : isPC ? 'headline-3' : 'headline-5'
          }
        >
          <NumberCounter
            value={amount}
            formatNumberFunction={(value: number | string) =>
              value < 1e6
                ? numberParse(value, 'currency')
                : numberParse(value, 'currencyLarge')
            }
          />
        </span>
      </div>
    </Wrapper>
  );
};
