import { bucketTimestamp } from '../../helpers/graph';

export const fetchAssetPrice = async (documentClient: any) => {
    const params_price = {
        TableName: "asset_prices",
        Select: "ALL_ATTRIBUTES"
    };
    try {
        const data = await (await documentClient.scan(params_price).promise())
        const result = await data.Items.reduce((r: any, a: any) => {
            r[a.ticker] = r[a.ticker] || [];
            r[a.ticker].push(a);
            return r;
        }, Object.create(null))
        Object.keys(result).forEach(key => result[key] = bucketTimestamp(result[key], 1800))
        return result
    } catch (err) {
        console.error(err)
        return {}
    }
}

