export const fetchBacktestAnalysis = async (documentClient: any) => {
    const param_performance = {
        TableName: "homora_performance_backtest",
        Select: "ALL_ATTRIBUTES"
    };
    try {
        const data = await (await documentClient.scan(param_performance).promise())
        const initial_price = data.Items![0].value
        const percentage_data = data.Items!.map((itm: any) => ({ timestamp_sec: itm.timestamp_sec, percentage: (itm.value - initial_price) / initial_price }))
        return percentage_data
    } catch (err) {
        console.error(err)
        return {}
    }
}

