import { BigNumber, utils } from 'ethers';
import { useQuery } from 'react-query';

import {
  homoraApi,
  HomoraApyResponse,
  homoraEndPoints,
  ironBankApi,
  ironBankApyResponse,
  ironBankEndPoints,
  ironBankRate,
} from '../../../api/axios/api-config';
import { getAxios } from '../../../api/axios/generic-api-calls';
import { StrategyInfoMap } from '../../../constants/strategy';
import { strategies } from '../../../constants/strategy/strategies';
import { BN } from '../../../helpers/utilities';
import { getStrategyInfo } from '../../../hooks/strategy/useStrategies';
import { keys } from '../../keyNames';

export function useFetchStrategies(options?: any) {
  return useQuery(
    [keys.strategies],
    async () => {
      const strategyInfos: StrategyInfoMap = {};
      const apys = (
        await getAxios<HomoraApyResponse>(homoraApi, homoraEndPoints.apys)
      ).data;
      const rates = (
        await getAxios<ironBankApyResponse>(
          ironBankApi,
          ironBankEndPoints.rates
        )
      ).data;
      try {
        for (const strategy of strategies) {
          const strategyInfo = await getStrategyInfo(strategy);
          if (!strategyInfo) continue;

          strategyInfos[strategy.strategyId] = strategyInfo;

          const APR_TJ = BN(apys[strategy.poolKey].totalAPY)
            .div(100)
            .plus(1)
            .ln();
          const TVL = strategyInfo.TVLInUnits;
          const interest_A = BN(
            rates.borrowRates.find(
              (rate: ironBankRate) =>
                rate.tokenSymbol.toLowerCase() ===
                strategyInfo.tokens[0].ticker.toLowerCase()
            )!.apy
          ).times(1.2);
          const interest_B = BN(
            rates.borrowRates.find(
              (rate: ironBankRate) =>
                rate.tokenSymbol.toLowerCase() ===
                strategyInfo.tokens[1].ticker.toLowerCase()
            )!.apy
          ).times(1.2);
          const debt_A = BN(
            utils.formatUnits(
              strategyInfo.debts[0],
              strategyInfo.tokens[0].decimals
            )
          ).times(strategyInfo.tokenATotokenB);
          const debt_B = BN(
            utils.formatUnits(
              strategyInfo.debts[1],
              strategyInfo.tokens[1].decimals
            )
          );
          const totalEquity = strategyInfo.totalEquityETHValue;
          /***********************************************************************************************
           *
           *  Using the following formula to calculate the APY:
           *  (APR_TJ * TVL - interest_A * debt_A - interest_B * debt_B) / totalEquity;
           *
           *  Using BigNumber.js to calculate avoid overflow in case of large TVL.
           *
           ***********************************************************************************************/
          const apr = BN(APR_TJ)
            .times(TVL)
            .minus(BN(interest_A).times(debt_A))
            .minus(BN(interest_B).times(debt_B))
            .div(totalEquity); // calculating apy
          strategyInfos[strategy.strategyId].apy = BN(apr)
            .exp()
            .minus(1)
            .toString();
          // .toFixed(2);
        }
      } catch (e: any) {
        // throw new Error(e.msg);
        console.log(e);
      }
      return strategyInfos;
    },

    {
      refetchOnMount: false,
      refetchOnReconnect: false,
      refetchOnWindowFocus: false,
      ...options,
    }
  );
}
