import CloseIcon from '@mui/icons-material/Close';
import { Grid, Modal, useMediaQuery } from '@mui/material';
import React, { useEffect, useMemo } from 'react';
import styled from 'styled-components';
import {
  DEFAULT_SLIPPAGE,
  InvestType,
  MIN_DEPOSIT_AMOUNT,
  MIN_WITHDRAW_AMOUNT,
} from '../../../../constants/crosschain';
import { StrategyInfo } from '../../../../constants/strategy';
import { useFetchAllTokens } from '../../../../features/tokens/hooks';
import {
  BN,
  isSlippageRelatedError,
  numberParse,
  slippageToNumber,
} from '../../../../helpers/utilities';

import useDebounce from '../../../../lib/uniswap/hooks/useDebounce';
import { useStore } from '../../../../store';
import { useSlippageToleranceWithDefault } from '../../../../store/base/application/hooks';
import textContext from '../../../../textContext';
import { PositionInfo } from '../../../../types';
import ApproveButton from '../../../Asset/ApproveButton';
import AssetSettings from '../../../Asset/AssetSettings';
import AssetStrategy from '../../../Asset/AssetStrategy';
// Comps
import InvestButton from '../../../Asset/InvestButton';
import WithdrawalSlider from '../../../Asset/WithdrawalSlider';
import { ModalProps } from '../../../layout/ModalsContainer';
import TransactionSettings from '../../Widgets/TransactionSettings/index';
import './AssetModal.scss';
import { QuestionMarkTooltip } from './toolTip';

const Close = styled('span')`
  margin: 0rem !important;
  position: absolute !important;
  right: 22px !important;
  top: 30px !important;
  cursor: pointer;
  @media (max-width: 1199px) {
    right: initial !important;
    left: 20px !important;
    top: 30px !important;
  }
`;

const FeeGrid = styled(Grid)`
  p {
    margin-block-start: 8px;
    margin-block-end: 8px;
  }
`;

export interface AssetProps {
  title: string;
  positionInfo: PositionInfo | null | undefined;
  invest?: boolean;
  initial?: string | number;
  strategyInfo: StrategyInfo;
  vaultItem: JSX.Element;
}

const LeftIcon = (
  <svg
    width="24"
    height="24"
    viewBox="0 0 24 24"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      d="M4 12H20M11 5L4 12L11 19"
      stroke="#F8F8F8"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    />
  </svg>
);

export default function Asset(props: AssetProps & ModalProps) {
  const language = useStore((state) => state.appSlice.language);
  const slippage = useSlippageToleranceWithDefault(DEFAULT_SLIPPAGE);
  const chainInfo = useStore((state) => state.chainSlice.chainInfo);
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );
  const approve = useStore((state) => state.chainSlice.chainProvider?.approve);
  const walletAddress = useStore(
    (state) => state.chainSlice.chainProvider?.walletAddress
  );
  const balance = useStore((state) => state.chainSlice.chainProvider?.balance);
  const investFn = useStore((state) => state.chainSlice.chainProvider?.invest);

  const text = textContext[language].assets;
  const { investText, withdrawText } = text;
  const {
    data: tokenInfoMap,
    isRefetching: isTokenRefetching,
    isLoading: tokenInfoIsLoading,
  } = useFetchAllTokens();

  const { title, positionInfo, onClose, invest, vaultItem, strategyInfo } =
    props;
  const selectedToken = useMemo(
    () =>
      strategyInfo &&
      strategyInfo.tokens &&
      strategyInfo.tokens[0] &&
      tokenInfoMap &&
      tokenInfoMap[strategyInfo.tokens[0].tokenContractAddress],
    [strategyInfo, tokenInfoMap]
  );

  const [amount, setAmount] = React.useState<string>(
    !invest ? positionInfo?.equity! : '0'
  );
  //TODO: Refactor debounce delay to a constant.
  const debouncedAmount = useDebounce(amount, 1000);

  const [isApproved, setIsApproved] = React.useState(false);
  const [gasEstimate, setGasEstimate] = React.useState('0');
  const [estimateError, setEstimateError] = React.useState('');
  const [sliderValue, setSliderValue] = React.useState(100);
  const debouncedWithdrawRatio = useDebounce(sliderValue / 100 + '', 1000);

  //TODO: Refactor isPC to useIsPC hook.
  const isPC = useMediaQuery('(min-width:1200px)');

  useEffect(() => {
    if (!strategyInfo || !investFn) return;
    if (invest && BN(selectedToken?.displayBalance ?? 0).lte(0)) return;
    const getGasPrice = async () => {
      setGasEstimate('0');
      setEstimateError('');
      try {
        const gasPrice: string = await investFn({
          strategyInfo,
          positionInfo: positionInfo ?? undefined,
          amount: invest
            ? debouncedAmount !== '0'
              ? debouncedAmount
              : '1'
            : debouncedWithdrawRatio
            ? debouncedWithdrawRatio !== '0'
              ? debouncedWithdrawRatio
              : '1'
            : '1',
          token: selectedToken!,
          slippage: slippageToNumber(slippage),

          type: invest
            ? positionInfo?.equity
              ? InvestType.INCREASE
              : InvestType.OPEN
            : InvestType.DECREASE,
          assetAddr: selectedToken?.tokenContractAddress,
          estimateGas: true,
        });
        setGasEstimate(gasPrice);
      } catch (err: any) {
        if (isSlippageRelatedError(err.toString())) {
          setEstimateError(
            `Your ${
              invest ? 'deposit' : 'withdraw'
            } failed due to slippage settings`
          );
        } else {
          // Failed with other reason
          if (!err.includes('transfer amount exceeds balance')) {
            setEstimateError(
              `Your ${
                invest ? 'deposit' : 'withdraw'
              } failed due to ${err.toString()}`
            );
          }
        }
      }
    };
    getGasPrice();
    console.log('getting gas estimate');
  }, [
    debouncedAmount,
    invest,
    selectedToken?.tokenContractAddress,
    debouncedWithdrawRatio,
    strategyInfo,
    selectedToken,
    slippage,
    positionInfo?.equity,
    investFn,
    positionInfo,
  ]);

  function calculateMaxDeposit() {
    const availableMax = BN(strategyInfo.capacity).minus(
      BN(strategyInfo.TVL).div(strategyInfo.leverage)
    );
    return BN(strategyInfo.maxDeposit).gt(availableMax)
      ? availableMax.toString()
      : strategyInfo.maxDeposit;
  }

  return (
    <>
      {/* {isOpen && ( */}
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={true}
        onClose={() => onClose()}
        sx={{
          '& .MuiBackdrop-root': { display: isPC ? 'flex' : 'none' },
        }}
      >
        <div className="assetModal">
          <div>
            <div className="assetHeightContainer">
              <Close onClick={() => onClose()}>
                {isPC ? <CloseIcon /> : LeftIcon}
              </Close>
              <h4 className="title">{invest ? investText : withdrawText}</h4>
              {/* {errMsg && <ErrorMessage />} */}
              <div className="contentContainer">
                <div className="assetContainer">
                  <Grid container direction="row">
                    <Grid item xs={12} sm={11}>
                      <AssetStrategy
                        vaultItem={vaultItem}
                        title={title}
                        leverage={strategyInfo.leverage}
                      />
                    </Grid>
                    <Grid sx={{ textAlign: 'right' }} item xs={0} sm={1}>
                      <TransactionSettings
                        mobile={!isPC}
                        strategyInfo={strategyInfo}
                        placeholderSlippage={DEFAULT_SLIPPAGE}
                      />
                    </Grid>
                  </Grid>

                  {invest && (
                    <AssetSettings
                      isLoading={tokenInfoIsLoading || isTokenRefetching}
                      token={selectedToken!}
                      invest={invest}
                      amount={amount}
                      setAmount={setAmount}
                      maxDeposit={calculateMaxDeposit()}
                    />
                  )}
                  {!invest && (
                    <WithdrawalSlider
                      {...{
                        positionInfo,
                        amount: Number(positionInfo?.equity)!,
                        maxAmount: Number(positionInfo?.equity)!,
                        token: selectedToken!,
                        setSliderValue,
                        sliderValue,
                        maxWithdraw: strategyInfo.maxWithdraw,
                      }}
                    />
                  )}

                  <Grid
                    justifyContent="space-between"
                    container
                    style={{ alignItems: 'center' }}
                  >
                    <Grid item style={{ lineHeight: '14px' }}>
                      <span className="subtitle-1">GAS FEE</span>
                      <QuestionMarkTooltip
                        mobile={!isPC}
                        title={`Gas Fee will be deducted from your wallet balance
                            directly (not from the current value of your
                            Aperture investment).`}
                        placementPC={'bottom-start'}
                        placementMobile={'bottom'}
                      />
                    </Grid>
                    <Grid item>
                      <div
                        className="subtitle-2"
                        style={{ lineHeight: '14px' }}
                      >
                        {chainInfo &&
                          `${BN(gasEstimate).lte(0) ? '--' : gasEstimate} ${
                            chainInfo.nativeDenom
                          }`}
                      </div>
                    </Grid>
                  </Grid>
                  <FeeGrid className="subtitle-2 medium" container>
                    <Grid container justifyContent="space-between" item>
                      <Grid item xs={10}>
                        <div style={{ marginBottom: '8px' }}>
                          Minimum Received After Slippage
                        </div>
                      </Grid>
                      <Grid item xs={2}>
                        <div style={{ textAlign: 'right' }}>
                          {numberParse(
                            Math.floor(
                              Number(
                                invest
                                  ? BN(amount).times(100).toString()
                                  : BN(positionInfo?.equity!)
                                      .times(sliderValue)
                                      .toString()
                              ) *
                                (1 - slippageToNumber(slippage))
                            ) / 100,
                            'currency'
                          )}
                        </div>
                      </Grid>
                    </Grid>
                    {/* <Grid container justifyContent="space-between" item>
                        <Grid item>
                          <div style={{ marginBottom: '8px' }}>
                            Swap Fee from the DEX
                          </div>
                        </Grid>
                        <Grid item>
                          <div>
                            {numberToPercentage(STRATEGY_FEE_DEX_SWAP, 1)}
                          </div>
                        </Grid>
                      </Grid> */}
                    <Grid container justifyContent="space-between" item>
                      <Grid item>
                        <div>Strategy Fee on Exit</div>
                      </Grid>
                      <Grid item>
                        <div>
                          {/* {numberToPercentage(STRATEGY_FEE_DEX_EXIT, 0)} */}
                          0
                        </div>
                      </Grid>
                    </Grid>
                  </FeeGrid>
                </div>
                {invest && (
                  <ApproveButton
                    tokenIsLoading={tokenInfoIsLoading || isTokenRefetching}
                    amount={amount}
                    token={selectedToken!}
                    isWalletConnected={isWalletConnected}
                    walletAddress={walletAddress ?? null}
                    isApproved={isApproved}
                    setIsApproved={setIsApproved}
                    approve={approve ?? null}
                  />
                )}

                {/* {isOpen && ( */}
                <InvestButton
                  tokenIsLoading={tokenInfoIsLoading || isTokenRefetching}
                  slippage={slippageToNumber(slippage)}
                  gasEstimate={gasEstimate}
                  amount={invest ? amount : sliderValue / 100 + ''}
                  invest={invest ?? false}
                  strategyInfo={strategyInfo}
                  positionInfo={positionInfo}
                  token={selectedToken ?? null}
                  isApproved={isApproved}
                  alertText={
                    estimateError !== ''
                      ? estimateError
                      : BN(gasEstimate).gt(balance ?? 0)
                      ? 'Insufficient Funds For Gas Fee'
                      : invest
                      ? !BN(amount).eq(0)
                        ? BN(amount).gt(calculateMaxDeposit())
                          ? `Not enough supply`
                          : BN(amount).gt(selectedToken?.displayBalance ?? 0)
                          ? `Insufficient ${selectedToken?.ticker} Balance`
                          : BN(amount).lt(MIN_DEPOSIT_AMOUNT)
                          ? `Minimum deposit amount is ${MIN_DEPOSIT_AMOUNT} ${selectedToken?.ticker}`
                          : ''
                        : ''
                      : BN(amount).lt(MIN_WITHDRAW_AMOUNT)
                      ? `Minimum withdraw amount is ${MIN_WITHDRAW_AMOUNT} ${selectedToken?.ticker}`
                      : ''
                  }
                />
                {/* )} */}
              </div>
            </div>
          </div>
        </div>
      </Modal>
      {/* )} */}
    </>
  );
}
