import { forwardRef, useRef, useState, useEffect } from 'react';
import { Box, NumberCounter, ButtonGroups } from '../../../../';
import { numberToPercentage } from '../../../../../../helpers/utilities';
import { LineGraph } from '../../../../Charts/LineGraph';
interface ReturnProps {
  color: any;
  data: any;
  payload: any;
  token: any;
}

export const Return = forwardRef<HTMLDivElement, ReturnProps>(
  ({
    color,
    data = { day: [], week: [], month: [], season: [], inception: [] },
    payload,
    token,
    ...props
  }, ref) => {

    const [timeRage, setTimeRage] = useState<String>('day');
    const parentRef = useRef(null);

    const getTime = () => {
      switch (timeRage) {
        case "day":
          return "1-day"
        case "week":
          return "1-week"
        case "month":
          return "1-month"
        case "season":
          return "3-month"
        case "inception":
          return "All"
      }
    }
    return (
      <Box ref={parentRef} {...props} className="back-test-anlysis">
        <div style={{ position: 'relative' }}>
          <ButtonGroups
            input={[{ text: '1D', value: 'day' }, { text: '1W', value: 'week' }, { text: '1M', value: 'month' }, { text: '3M', value: 'season' }, { text: 'ALL', value: 'inception' },]}
            onSelect={(value: string) => setTimeRage(value)}
          />
        </div>
        <div className="headline-4 semiBold">Vault Return</div>
        <div className="headline-5" style={{ marginBottom: '33px' }}>
          Return:
          <span className={`span-1 ${data[timeRage][data[timeRage].length - 1].share_price >= data[timeRage][0].share_price ? ' positive' : ' negative'}`}>
            <NumberCounter
              value={(data[timeRage][data[timeRage].length - 1].share_price - data[timeRage][0].share_price) / data[timeRage][0].share_price}
              formatNumberFunction={(value: number) =>
                `${value > 0 ? '+' : ''}${numberToPercentage(value)}`
              }
            />
          </span>
          Past {getTime()}
        </div>
        <div className="graph-title">
          <div className="subtitle-2">Return(%)</div>
          <div className="headline-5 float-right">{token} Price($)</div>
        </div>

        <LineGraph
          color={color}
          data={data[timeRage]}
          referenceAmount={0}
          type="full"
          legend
          payload={payload}
          token={token}
          lineCount={2}
        />
      </Box>
    );
  }
)
