import { createContext, useCallback, useEffect, useState } from 'react'
import * as AWS from 'aws-sdk';
import fetchGraphdata from '../../api/graph';

export const AWSDocumentClientContext = createContext({});

const configuration = {
    accessKeyId: process.env.REACT_APP_AWS_ACCESS_KEY,
    secretAccessKey: process.env.REACT_APP_AWS_SECRET_KEY,
    region: "us-west-2"
};
AWS.config.update(configuration);
const documentClient = new AWS.DynamoDB.DocumentClient();


export const AWSProvider: React.FC<React.PropsWithChildren> = ({ children }) => {
    const initialState: any = {
        graphData: {}
    }

    useEffect(() => {
        // declare the data fetching function
        const fetchData = async () => {
            const graphData = await fetchGraphdata(documentClient)
            setState((prevState) => ({
                ...prevState,
                graphData
            }))
        }

        // call the function
        fetchData()
    }, [])


    const [state, setState] = useState(initialState)

    const setGraphData = useCallback(async () => {
        const graphData = await fetchGraphdata(documentClient)
        setState((prevState) => ({
            ...prevState,
            graphData
        }))
    }, [])

    return <AWSDocumentClientContext.Provider value={{ state, setGraphData }}>
        {children}
    </AWSDocumentClientContext.Provider>
}
