import React from 'react';
import textContext from '../../textContext';
import { useStore } from '../../store';
import { wallet } from '../../assets/images/wallet';
// Comps
import { BN, deepCopy } from '../../helpers/utilities';
import PositionList from './PositionList';
import { Box } from '../v1';
import './styles/Position.scss';
import { styled, useMediaQuery } from '@mui/material';
import TableLoader from '../Investment/TableLoader';
import { useFetchEvmPositions } from '../../features/positions/hooks';
import { useFetchStrategies } from '../../features/strategies/hooks';
import { PositionInfoMap } from '../../constants/position';
import { PositionInfo } from '../../types';
import { useFetchEvmExtraPositions } from '../../features/positions/hooks/useFetchExtraPositions';

// TODO: add storybook
const LoadingContainer = styled(Box)`
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  /* text-align: center; */
  /* padding: 8% 24px; */
  /* grid-gap: 32px; */
  /* gap: 32px; */
  color: #cad2d8;
  background: rgba(13, 23, 24, 0.55);
  border-radius: 0px 0px 8px 8px;
`;

export default function PositionContainer() {
  const language = useStore((state) => state.appSlice.language);
  const text = textContext[language as keyof object]['portfolio']['reminder'];
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );
  const loaders = new Array(6).fill('');

  const isPC = useMediaQuery('(min-width:1200px)');

  const {
    data: strategies,
    // refetch: strategiesRefetch,
    // isLoading: strategiesIsLoading,
  } = useFetchStrategies({
    // refetchInterval: 30000, // 30 seconds
    // retryDelay: 1000, // 1 second
  });

  const positionResults = useFetchEvmPositions();

  const extraPositionResults = useFetchEvmExtraPositions();

  const positionsIsLoading =
    positionResults.some((r) => !r.data) ||
    extraPositionResults.some((r) => !r.data);

  const positions: PositionInfoMap = (() => {
    return (
      (positionResults.map((r) => r.data ?? null) as PositionInfo[]).filter(
        (x) => x !== null && BN(x.equity).gt(0)
      ) as PositionInfo[]
    ).reduce((acc: PositionInfoMap, cur: PositionInfo): PositionInfoMap => {
      let { strategyId } = cur;
      return { ...acc, [strategyId]: cur };
    }, {}) as PositionInfoMap;
  })();

  const extraPositions: PositionInfo[] = (() => {
    return (
      extraPositionResults.map((r) => r.data ?? null) as PositionInfo[]
    ).filter((x) => x !== null && BN(x.equity).gt(0));
  })();

  if (!isWalletConnected) {
    return (
      <Box className="reminder-container">
        <div
          style={{
            alignItems: 'center',
            display: 'flex',
          }}
        >
          <img
            src={wallet}
            width="22.5"
            height="22.5"
            alt="connect your wallet"
          />
          <span
            className={isPC ? 'headline-4 medium' : 'headline-5'}
            style={{
              paddingLeft: '10px',
              whiteSpace: 'nowrap',
            }}
          >
            {text.connectWallet}
          </span>
        </div>
      </Box>
    );
  }

  const IsPostionNotEmpty =
    (positions &&
      Object.keys(positions).length !== 0 &&
      Object.keys(positions).find(
        (key) => Number(positions[key].equity) !== 0
      )) ||
    (extraPositions?.length > 0 &&
      extraPositions.find((p) => Number(p.equity) !== 0));

  return (
    <>
      {positionsIsLoading || !strategies || !positions || !extraPositions ? (
        <LoadingContainer>
          {loaders.map((d, i) => (
            <TableLoader key={i} />
          ))}
        </LoadingContainer>
      ) : IsPostionNotEmpty ? (
        <PositionList
          extraPositions={extraPositions ?? null}
          positions={positions}
          data={Object.values(strategies)}
          strategyType={'PDN'}
        />
      ) : (
        <Box className="reminder-container">
          <div style={{ textAlign: 'center' }}>
            <span className={isPC ? 'headline-4 medium' : 'headline-5'}>
              {text.noPosition}
            </span>
          </div>
        </Box>
      )}
    </>
  );
}
