import './styles/AssetSettings.scss';

import InputBase from '@mui/material/InputBase';
import React from 'react';
import NumberFormat from 'react-number-format';
import styled from 'styled-components';

import { lockIcon } from '../../assets/images';
import { MIN_DEPOSIT_AMOUNT } from '../../constants/crosschain';
import { SerializedTokenInfo } from '../../constants/tokenConfig';
import {
  BN,
  countDecimals,
  getDigits,
  numberParse,
  numberParseLarge,
} from '../../helpers/utilities';
import { useStore } from '../../store';
import textContext from '../../textContext';
import TableLoader from '../Investment/TableLoader';
import { Button } from '../v1';
import { QuestionMarkTooltip } from '../v1/Modals/AssetModal/toolTip';

interface AssetSettingsProps {
  invest?: boolean;
  amount: string;
  setAmount: React.Dispatch<React.SetStateAction<string>>;
  withdrawValue?: string;
  token: SerializedTokenInfo;
  maxDeposit: string;
  isLoading: boolean;
}
export default function AssetSettings(props: AssetSettingsProps) {
  const {
    invest,
    amount,
    setAmount,
    withdrawValue,
    token,
    maxDeposit,
    isLoading,
  } = props;
  const language = useStore((state) => state.appSlice.language);
  const text = textContext[language].assets;
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const { tokenText } = text;
  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLDivElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleNumber = (val: string, decimals = 6) => {
    let parsed = val === '' ? '0' : val.replace(/,/g, '').substring(1);
    if (BN(parsed).gte(0) && countDecimals(parsed)! <= decimals) {
      setAmount(parsed);
    }
  };
  const LoaderContainer = styled('div')`
    margin-left: 10px;
    display: flex;
    width: 20px;
    top: -10px;
    position: relative;
  `;
  const MaxContainer = styled('div')`
    display: flex; //grid;
    // grid-template-columns: auto 50px;
    @media (max-width: 1199px) {
      position: absolute;
      /* top: 70px; */
      width: 100%;
      left: 0;
      flex-direction: column;
      align-items: flex-end;
      top: 0;
      flex-direction: column;
      just-content: end;
    }
  `;

  const MaxBalance = styled('div').attrs({
    className: 'subtitle-2 light-grey',
  })`
    display: flex;
    white-space: nowrap;
    margin: 9px 8px 0px 12px;
    @media (max-width: 1199px) {
      /* margin: 0; */
      margin: auto 16px;
    }
  `;
  const MaxBalanceContainer = styled('div').attrs({})`
    @media (max-width: 1199px) {
      height: 53px;
      margin: auto 0;
      display: flex;
    }
  `;
  const MaxSupply = styled('div').attrs({
    className: 'subtitle-2 medium',
  })`
    width: 100%;
    text-align: right;
    @media (max-width: 1199px) {
      text-align: left;
      margin-block: 10px;
    }
  `;

  return (
    <>
      <div
        className={
          (BN(amount).gte(MIN_DEPOSIT_AMOUNT) &&
            BN(amount).lte(invest ? token?.displayBalance : withdrawValue!) &&
            BN(amount).lte(invest ? maxDeposit : Infinity)) ||
          BN(amount).eq(0)
            ? 'token'
            : 'token errorColor'
        }
      >
        <div
          onClick={(e) => handleClick(e)}
          aria-describedby="token-popover"
          className="selectToken"
        >
          <div className="textUpper semiBold subtitle-1">{tokenText}</div>
          <div
            className="flex"
            style={{
              justifyContent: 'flex-start',
              alignItems: 'center',
              marginTop: '13px',
            }}
          >
            {token && <img src={token.icon} alt={token.ticker} width="30" />}
            <h4 style={{ padding: '1px .5rem' }}>
              {token && token.ticker.toUpperCase()}
            </h4>
          </div>
        </div>
        <div className={`selectValue ${!invest ? 'withdraw' : ''}`}>
          {!invest && <img src={lockIcon} alt="lock icon" />}
          <NumberFormat
            value={amount}
            onChange={(event) =>
              handleNumber(event.target.value, token?.decimals ?? 6)
            }
            customInput={InputBase}
            type="text"
            decimalScale={token?.decimals ?? 6}
            placeholder={'0.00'}
            thousandSeparator={true}
            // disabled={currentValue ? true : false}
            prefix="$"
          />
          {/* {!invest && <p>{partialWithdrawl}</p>} */}
          {invest && (
            <MaxContainer>
              <MaxBalanceContainer>
                <MaxBalance>
                  {/* TODO: Refactor calculation using BigNumber.js. */}
                  {!isLoading ? (
                    `Balance : ${
                      getDigits(Number(token?.displayBalance)) > 3
                        ? '$' +
                          numberParseLarge(Number(token?.displayBalance), 2)
                        : numberParse(
                            Math.floor(Number(token?.displayBalance) * 100) /
                              100,
                            'currency'
                          )
                    }`
                  ) : (
                    <>
                      Balance :{' '}
                      <LoaderContainer>
                        <TableLoader height={20} />{' '}
                      </LoaderContainer>
                    </>
                  )}
                </MaxBalance>
              </MaxBalanceContainer>
              <Button
                disabled={!invest && !withdrawValue}
                onClick={() => {
                  setAmount(
                    withdrawValue
                      ? withdrawValue
                      : (BN(token?.displayBalance).gt(maxDeposit)
                          ? BN(maxDeposit)
                          : BN(token?.displayBalance)
                        ).toString()
                  );
                }}
                className="textUpper"
                style={{ height: '32px' }}
              >
                <span className="button-3 medium">{`Max`}</span>
              </Button>
            </MaxContainer>
          )}
        </div>
        <MaxSupply>
          <span>
            Max Deposit:
            {maxDeposit ? numberParse(maxDeposit, 'currency') : '--'}
          </span>
          <QuestionMarkTooltip
            title={`If you’d like to deposit more than the max, you can do it over
              multiple transactions. This is to limit the price impact from
              opening a large position.`}
            placementPC={'bottom-end'}
            placementMobile={'bottom-end'}
          />
        </MaxSupply>
      </div>
    </>
  );
}
