import { AWSDocumentClientContext } from './../../store/provider/AWSProvider';
import { useContext } from 'react';
import { fetchAssetPrice, fetchHomoraPerformace, fetchBacktestAnalysis, fetchWhiteList } from './graph'

const fetchGraphdata = async (documentClient: any) => {
    const data = {
        assetPrice: await fetchAssetPrice(documentClient),
        homoraPerformance: await fetchHomoraPerformace(documentClient),
        backtestAnalysis: await fetchBacktestAnalysis(documentClient),
        whiteList: await fetchWhiteList(documentClient),
    }
    return data
}

export default fetchGraphdata