const FeeGrid = styled(Grid)`
  grid-gap: 12px;
  p {
    margin-block-start: 0px !important;
    margin-block-end: 0px !important;
  }
`;

interface TransactionSettingsProps {
  mobile: boolean;
  strategyInfo: StrategyInfo;
  placeholderSlippage: Percent; // varies according to the context in which the settings dialog is placed
}

const THREE_DAYS_IN_SECONDS = ms`3 days` / 1000;

export default function TransactionSettings({
  mobile,
  strategyInfo,
  placeholderSlippage,
}: TransactionSettingsProps) {
  const isPC = useMediaQuery('(min-width:1200px)');
  const stableCoin = strategyInfo.tokens[0].ticker.toUpperCase();
  const token = strategyInfo.tokens[1].ticker.toUpperCase();
  const userSlippageTolerance = useSlippageTolerance();
  const setUserSlippageTolerance = useSetSlippageTolerance();
  const [deadline, setDeadline] = useTransactionTTL();

  const [slippageInput, setSlippageInput] = useState('');
  const [slippageError, setSlippageError] = useState<SlippageError | false>(
    false
  );

  const [tooLow, setTooLow] = useState(false);
  const [tooHigh, setTooHigh] = useState(false);

  const [deadlineInput, setDeadlineInput] = useState('');
  const [deadlineError, setDeadlineError] = useState<DeadlineError | false>(
    false
  );

  const [showSettings, setShowSettings] = useState(false);
  // const inputRef = createRef<HTMLInputElement>();
  // useEffect(() => {
  //   if (inputRef && inputRef.current) inputRef.current.focus();
  // }, [inputRef.current]);

  function parseSlippageInput(value: string) {
    // populate what the user typed and clear the error
    setSlippageError(false);
    setTooLow(false);
    setTooHigh(false);

    if (value.length === 0) {
      setUserSlippageTolerance('auto');
    } else {
      const parsed = Math.floor(Number.parseFloat(value) * 100);
      if (!Number.isInteger(parsed) || parsed < 0 || parsed > 5000) {
        setUserSlippageTolerance('auto');
        if (value !== '.') {
          setSlippageError(SlippageError.InvalidInput);
        }
      } else {
        setUserSlippageTolerance(new Percent(parsed, 10_000));
      }

      setTooLow(value !== 'auto' && Number.parseFloat(value) <= 0.1);
      setTooHigh(value !== 'auto' && Number.parseFloat(value) >= 3);
    }
  }

  function parseCustomDeadline(value: string) {
    // populate what the user typed and clear the error
    setDeadlineInput(value);
    setDeadlineError(false);

    if (value.length === 0) {
      setDeadline(DEFAULT_DEADLINE_FROM_NOW);
    } else {
      try {
        const parsed: number = Math.floor(Number.parseFloat(value) * 60);
        if (
          !Number.isInteger(parsed) ||
          parsed < 60 ||
          parsed > THREE_DAYS_IN_SECONDS
        ) {
          setDeadlineError(DeadlineError.InvalidInput);
        } else {
          setDeadline(parsed);
        }
      } catch (error) {
        console.error(error);
        setDeadlineError(DeadlineError.InvalidInput);
      }
    }
  }

  // const showCustomDeadlineRow = Boolean(chainId && !L2_CHAIN_IDS.includes(chainId))
  const showCustomDeadlineRow = false;

  const SettingButton = (props: any) => (
    <SettingsButton onClick={props.onClick}>{SettingIcon}</SettingsButton>
  );

  return (
    <>
      {/* {mobile ? (
        <>
          <SettingButton onClick={() => setShowSettings(true)} />
          <MobilePopOver
            open={showSettings}
            onClose={() => setShowSettings(false)}
            cross={true}
            sx={{ padding: 0 }}
            className="transaction-setting-pop-over"
          >
            <Content />
          </MobilePopOver>
        </>
      ) : (*/}
      <Dropdown
        popClassName="pop-transaction-setting"
        direction={{
          anchorOrigin_vertical: isPC ? 'top' : 'bottom',
          anchorOrigin_horizontal: 'left',
          transformOrigin_vertical: 'top',
          transformOrigin_horizontal: 'right',
        }}
        trigger={<SettingButton />}
      >
        <div className="settingModal">
          <h5 className="title">transaction settings</h5>
          <div className="settingContainer">
            <Grid container className="info-grid">
              <Grid item>
                <InfoOutlinedIcon fontSize="small" />
              </Grid>
              <Grid item xs>
                <p>
                  This strategy may involve one or more swaps to open the
                  position. Set your desired slippage tolerance below.
                </p>
              </Grid>
            </Grid>
            <Grid container className="value-setting" item>
              <Grid container item gap="sm" alignItems={'center'}>
                <Grid item>
                  <p className="subtitle-2">Slippage tolerance</p>
                </Grid>
                <Grid item>
                  <QuestionMarkTooltip
                    title={`The amount of ${stableCoin} received is subject to the liquidity of ${stableCoin} and ${token}. The default slippage tolerance is set to ${
                      slippageToNumber(DEFAULT_SLIPPAGE) * 100
                    }%.`}
                    placementPC={'bottom'}
                    placementMobile={'bottom'}
                    toolTipClassName={'tolerance-tooltip'}
                  />
                </Grid>
              </Grid>
              <Grid container item gap="sm" className="input-grid">
                <Grid
                  item
                  className={`number-input${
                    slippageError || tooLow || tooHigh ? ' errorColor' : ''
                  }`}
                >
                  <NumberFormat
                    value={slippageInput}
                    onChange={(e) => setSlippageInput(e.target.value)}
                    customInput={InputBase}
                    type="text"
                    decimalScale={2}
                    placeholder={
                      slippageToNumber(
                        userSlippageTolerance === 'auto'
                          ? placeholderSlippage
                          : userSlippageTolerance
                      ) *
                        100 +
                      '%'
                    }
                    suffix="%"
                    allowNegative={false}
                    onBlur={() => parseSlippageInput(slippageInput)}
                  />
                </Grid>
                <Grid item>
                  <Button
                    variant="contained"
                    size="small"
                    text={'AUTO'}
                    onClick={() => {
                      setSlippageInput('');
                      parseSlippageInput('');
                    }}
                    style={{ width: isPC ? '88px' : '65px' }}
                  ></Button>
                </Grid>
                {slippageError || tooLow || tooHigh ? (
                  <Grid item>
                    <div className="error-view">
                      <img
                        src={warningIcon}
                        aria-label="warning"
                        style={{ paddingRight: '6px' }}
                      />
                      {slippageError ? (
                        <span className="body-1">
                          Enter a valid slippage percentage
                        </span>
                      ) : tooLow ? (
                        <span className="body-1">
                          Your transaction may fail
                        </span>
                      ) : (
                        <span className="body-1">
                          Your transaction may be frontrun
                        </span>
                      )}
                    </div>
                  </Grid>
                ) : null}
              </Grid>
            </Grid>
            {/* {showCustomDeadlineRow && (
              <Grid
                container
                className="value-setting"
                item
                style={{ paddingTop: '0px' }}
              >
                <p className="subtitle-2">Transaction Deadline</p>
                <Grid container item gap="sm" className="input-grid">
                  <Grid item className="number-input">
                    <NumberFormat
                      placeholder={(DEFAULT_DEADLINE_FROM_NOW / 60).toString()}
                      value={
                        deadlineInput.length > 0
                          ? deadlineInput
                          : deadline === DEFAULT_DEADLINE_FROM_NOW
                            ? ''
                            : (deadline / 60).toString()
                      }
                      onChange={(e) => parseCustomDeadline(e.target.value)}
                      customInput={InputBase}
                      type="text"
                      allowNegative={false}
                    />
                  </Grid>
                  <Grid item>
                    <span>minutes</span>
                  </Grid>
                </Grid>
              </Grid>
            )} */}
          </div>
        </div>
      </Dropdown>
      {/* )} */}
    </>
  );
}
