import React from 'react';
import {
  Area,
  AreaChart,
  CartesianGrid,
  Legend,
  ReferenceLine,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';

import { Tooltip as CustomTooltip } from './Tooltip';

export interface LGProps {
  type: 'full' | 'mini';
  data: any;
  fill?: boolean;
  legend?: boolean;
  payload?: any;
  referenceAmount?: number;
  color: any;
  token?: string | null;
  lineCount: 1 | 2;
}

export const LineGraph = ({
  type = 'full',
  data = [],
  legend = false,
  fill = false,
  referenceAmount = undefined,
  payload,
  color = { percentage: '#7BEFFF', price: '#19EE94' },
  token,
  lineCount,
  ...props
}: LGProps) => {
  const [xy, setXY] = React.useState([0, 0]);
  const parentRef = React.useRef(null);

  const random = Math.round(Math.random() * 1000);
  React.useLayoutEffect(() => {
    function updateSize() {
      const element = document.getElementById(`line-chart-parent-${random}`);
      console.log();
      if (element) setXY([element.offsetWidth, element.offsetWidth * 0.4]);
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, []);

  const ran1 = Math.random() * 100;
  const ran2 = Math.random() * 100;

  const getMaxPirceObject = (data: Object[]): Object | null =>
    data.reduce((prev, curr) => (prev.price > curr.price ? prev : curr));
  const getMinPirceObject = (data: Object[]): Object | null =>
    data.reduce((prev, curr) => (prev.price < curr.price ? prev : curr));

  const tickArray = (data: Object[]): number[] => {
    const count = type === 'mini' ? 4 : 9;
    if (!data || data.length === 0) {
      return [];
    }
    const max = getMaxPirceObject(data)!.price + 0.5;
    const min = getMinPirceObject(data)!.price - 0.5;
    const result = Array.from(
      { length: count },
      (_, i) => (i * (max - min)) / (count - 1) + min
    );

    return result;
  };

  return (
    <div
      id={`line-chart-parent-${random}`}
      ref={parentRef}
      style={{ width: '100%' }}
      {...props}
    >
      <ResponsiveContainer width={xy[0]} height={xy[1]}>
        <AreaChart
          data={data}
          margin={{
            top: 5,
            right: type === 'mini' ? 10 : 20,
            left: 20,
            bottom: 5,
          }}
        >
          <Tooltip
            content={<CustomTooltip type={type} token={token} color={color} />}
            cursor={{
              stroke: 'white',
              strokeWidth: '0.75',
              strokeLinecap: 'round',
              strokeDasharray: '5 5',
            }}
          />
          <CartesianGrid
            vertical={false}
            style={{
              opacity: '0.2',
              strokeWidth: '0.75',
              strokeLinecap: 'round',
              strokeDasharray: '3 3',
            }}
          />
          {referenceAmount !== undefined && (
            <ReferenceLine y={0} opacity="0.6" />
          )}
          {legend && <Legend payload={payload} />}
          <XAxis
            dataKey="timestamp_sec"
            interval={
              type === 'mini'
                ? Math.round(data.length / 4)
                : Math.round(data.length / 10)
            }
            color="white"
            height={type === 'mini' ? 20 : 30}
            strokeWidth={2}
            tick={{ fontSize: type === 'mini' ? '12px' : '14px' }}
            tickFormatter={(tick) => {
              try {
                const date = new Date(tick * 1000).toUTCString().split(' ');
                return `${date[2]} ${date[1]}`;
              } catch (e) {
                console.error(e);
                return tick;
              }
            }}
          />

          <YAxis
            dataKey="percentage"
            tickCount={type === 'mini' ? 4 : 9}
            width={type === 'mini' ? 30 : 50}
            tick={{ fontSize: type === 'mini' ? '12px' : '14px' }}
            tickFormatter={(tick) => {
              return `${(Math.round(tick * 10000) / 100).toFixed(2)}%`;
            }}
            strokeWidth={2}
          />

          {lineCount === 2 && (
            <YAxis
              dataKey="price"
              width={30}
              yAxisId="right"
              orientation="right"
              tick={{ fontSize: '14px' }}
              strokeWidth={2}
              type="number"
              domain={['dataMin', 'dataMax']}
              tickFormatter={(tick) => {
                return `${(Math.round(tick * 100) / 100).toFixed(2)}`;
              }}
              ticks={tickArray(data)}
            />
          )}

          <Area
            type="monotone"
            dataKey={'percentage'}
            stroke={color.percentage}
            fillOpacity={1}
            fill={`url(#percentage${ran1})`}
            strokeWidth={1}
          />
          <defs>
            <linearGradient
              id={'percentage' + ran1}
              x1="0"
              y1="0"
              x2="0"
              y2="1"
            >
              <stop
                offset="5%"
                stopColor={color.percentage}
                stopOpacity={fill ? 0.4 : 0}
              />
              <stop offset="95%" stopColor={color.percentage} stopOpacity={0} />
            </linearGradient>
          </defs>

          {lineCount === 2 && (
            <>
              <Area
                type="monotone"
                yAxisId="right"
                dataKey={'price'}
                stroke={color.price}
                fillOpacity={1}
                fill={`url(#price${ran2})`}
                strokeDasharray="6 2"
                strokeWidth={1}
              />
              <defs>
                <linearGradient id={'price' + ran2} x1="0" y1="0" x2="0" y2="1">
                  <stop
                    offset="5%"
                    stopColor={color.price}
                    stopOpacity={fill ? 0.4 : 0}
                  />
                  <stop offset="95%" stopColor={color.price} stopOpacity={0} />
                </linearGradient>
              </defs>
            </>
          )}
        </AreaChart>
      </ResponsiveContainer>
    </div>
  );
};
